import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AbstractControl, ValidationErrors, FormControl, Validators, Validator, ControlValueAccessor } from '@angular/forms';
import { OnDestroy } from '@angular/core';

export class BaseControlValueAccessorUtils implements OnDestroy, ControlValueAccessor, Validator {
    baseInput: FormControl = new FormControl(Validators.required);
    onDestroy = new Subject<void>();

    ngOnDestroy(): void {
        this.onDestroy.next();
        this.onDestroy.complete();
    }

    onTouched = () => { };

    writeValue(value: any): void {
        if (value) {
            this.baseInput.setValue(value, { emitEvent: false });
        }
    }

    registerOnChange(fn: any): void {
        this.baseInput.valueChanges.pipe(takeUntil(this.onDestroy)).subscribe(fn);
    }

    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }

    setDisabledState?(isDisabled: boolean): void {
        isDisabled ? this.baseInput.disable() : this.baseInput.enable();
    }

    validate(control: AbstractControl): ValidationErrors | null {
        return control.valid ? null : { invalid: true };
    }
}
