import { FormGroup, FormControl } from '@angular/forms';

export class Utils {
    static markFormGroupTouched(formGroup: FormGroup): void {
        (Object as any).values(formGroup.controls).forEach((control: FormControl | FormGroup) => {
            control.markAsTouched();

            const childForm = control as FormGroup;
            if (childForm.controls) {
                this.markFormGroupTouched(childForm);
            }
        });
    }

    static removeReference(value: any): any {
        return JSON.parse(JSON.stringify(value));
    }

    static sendMoney(value: number): number {
        return +(value * 100).toFixed(0);
    }

    static receiveMoney(value: number): number {
        return value / 100;
    }
}
