import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { MatBottomSheetRef } from '@angular/material/bottom-sheet';
import { FormControl, Validators } from '@angular/forms';
import { Utils } from 'src/app/shared/utils/utils';

@Component({
    selector: 'app-need-change-money',
    templateUrl: './need-change-money.component.html',
    styleUrls: ['./need-change-money.component.scss']
})
export class NeedChangeMoneyComponent implements OnInit {
    @ViewChild('inputValue') inputValue: ElementRef;
    controlValue: FormControl = new FormControl(0, [Validators.required, Validators.max(63.80)]);
    haveChange = false;
    value = 0;

    constructor(
        private matBottomSheetRef: MatBottomSheetRef<NeedChangeMoneyComponent>
    ) { }

    ngOnInit(): void {
    }

    close(): void {
        this.matBottomSheetRef.dismiss();
    }

    onHaveChange(): void {
        this.haveChange = true;
        setTimeout(() => {
            this.inputValue.nativeElement.focus();
        }, 100);
    }

    setChangeValue(): void {
        if (this.controlValue.invalid) {
            return;
        }

        this.matBottomSheetRef.dismiss(Utils.sendMoney(this.controlValue.value));
    }
}
