import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Order } from 'src/app/shared/interfaces/order';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
    selector: 'app-add-item',
    templateUrl: './add-item.component.html',
    styleUrls: ['./add-item.component.scss'],
})
export class AddItemComponent implements OnInit {
    @Output() changeOrder: EventEmitter<Partial<Order>> = new EventEmitter();

    @Input() price: number;
    @Input() set disabled(status: boolean) {
        this.isDisabled$.next(status);
    }

    isDisabled$ = new BehaviorSubject<boolean>(false);
    qty = new BehaviorSubject<number>(1);

    constructor(
        private matSnackBar: MatSnackBar
    ) { }

    ngOnInit(): void { }

    remove(): void {
        if (!this.isDisabled()) {
            const currentQty = this.qty.getValue();

            if (currentQty !== 0) {
                this.qty.next(currentQty - 1);
            }
        }
    }

    add(): void {
        if (!this.isDisabled()) {
            const currentQty = this.qty.getValue();
            this.qty.next(currentQty + 1);
        }
    }

    onClick(): void {
        if (!this.isDisabled()) {
            const order: Order = {
                qty: this.qty.getValue(),
                totalPrice: this.price * this.qty.getValue()
            };

            this.changeOrder.emit(order);
        }
    }

    isDisabled(): boolean {
        const isDisabled = this.isDisabled$.getValue();
        return isDisabled;
    }

    alertWarning(): void {
        this.matSnackBar.open('É preciso selecionar todos os itens obrigatórios para adicionar o pedido.', '', {
            duration: 2000,
            horizontalPosition: 'center',
            verticalPosition: 'top',
        });
    }
}
