import { Component, OnInit, ViewChild, ElementRef, Inject, AfterContentInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { MatStepper } from '@angular/material/stepper';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { NeedChangeMoneyComponent } from './need-change-money/need-change-money.component';
import { SelectOption } from 'src/app/shared/interfaces/select-option';
import { Client } from '../shared/interfaces/client';
import { RequestOrder } from '../shared/interfaces/request-order';
import { PaymentType } from 'src/app/shared/interfaces/payment-type';
import { Company } from 'src/app/shared/interfaces/company';
import { MatSnackBar } from '@angular/material/snack-bar';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { ClientService } from '../shared/services/client.service';
import { CheckoutService } from '../shared/services/checkout.service';

@Component({
    selector: 'app-delivery-data',
    templateUrl: './delivery-data.component.html',
    styleUrls: ['./delivery-data.component.scss']
})
export class DeliveryDataComponent implements OnInit, OnDestroy, AfterContentInit {
    @ViewChild('stepper') private stepper: MatStepper;
    @ViewChild('inputNumber') inputNumber: ElementRef;
    localStorageUserKey = 'cardapinStoreUser';
    formData: FormGroup;
    formAddress: FormGroup;
    selectedIndex = 0;
    requestOrderSummary: RequestOrder;
    isEdit = false;
    isNew = true;
    company: Company;
    totalPrice: number;
    optionWithdrawalSelected: SelectOption;
    optionsWithdrawal: SelectOption[] = [
        {
            name: 'Entrega',
            value: 'delivery',
            description: 'sujeito a disponibilidade e taxas'
        },
        {
            name: 'Retirada no local',
            value: 'withdrawn',
            description: 'sujeito a disponibilidade'
        }
    ];
    addressInfo = {
        both: 'Informe seu endereço para entrega do pedido ou selecione a opção retirada no local.',
        delivery: 'Informe seu endereço para entrega do pedido.',
        withdrawn: 'Esté estabelecimento apenas trabalha com retirada do pedido no local.'
    };
    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private matDialogRef: MatDialogRef<DeliveryDataComponent>,
        private formBuilder: FormBuilder,
        private matBottomSheet: MatBottomSheet,
        private matSnackBar: MatSnackBar,
        private clientService: ClientService,
        private checkoutService: CheckoutService,
        @Inject(MAT_DIALOG_DATA) data: {
            selectedIndex: number,
            requestOrder: RequestOrder,
            company: Company
        }
    ) {
        this.company = data.company;

        if (data?.requestOrder) {
            this.selectedIndex = data.selectedIndex;
            this.requestOrderSummary = data.requestOrder;
            this.isEdit = true;
            this.optionWithdrawalSelected = this.requestOrderSummary.withdrawal ? this.optionsWithdrawal[1] : this.optionsWithdrawal[0];
        }

        this.checkoutService.get$.subscribe(res => {
            this.totalPrice = res.orderPriceTotal;
        });
    }

    ngOnInit(): void {
        const client = this.requestOrderSummary?.client;

        this.formData = this.formBuilder.group({
            phone: [this.isEdit ? client.phone : '', [Validators.required, Validators.minLength(10)]],
            name: [this.isEdit ? client.name : '', [Validators.required, Validators.minLength(4)]]
        });

        const address = this.requestOrderSummary?.client;

        this.formAddress = this.formBuilder.group({
            address: [this.isEdit ? address.address : '', Validators.required],
            area: [this.isEdit ? address.area : '', Validators.required],
            number: [this.isEdit ? address.number : '', Validators.required],
            hasNumber: [this.isEdit ? !address.number : ''],
            complement: [this.isEdit ? address.complement : ''],
            landmark: [this.isEdit ? address.landmark : '']
        });

        const clientData = JSON.parse(localStorage.getItem(this.localStorageUserKey)) as Client;

        if (clientData) {
            this.formData.get('phone').setValue(String(clientData.phone));
            this.setClientData(clientData);
        }

        this.searchUserByPhone();
    }

    close(): void {
        this.matDialogRef.close();
    }

    searchUserByPhone(): void {
        this.formData.get('phone').valueChanges.pipe(
            debounceTime(500),
            takeUntil(this.destroy$),
        ).subscribe((valuePhone) => {

            if (this.formData.get('phone').valid) {
                this.clientService.get(valuePhone).subscribe(c => {
                    if (c != null && c.phone != null) {
                        this.setClientData(c);
                        this.saveClientInLocalStorage(c);
                    } else {
                        this.isNew = true;
                    }
                });
            }
        });
    }

    // STEP USER DATA
    userDataStepNext(): void {
        if (this.formData.invalid) {
            return;
        }

        this.nextOrCloseDialog();
    }

    // STEP ADDRESS
    onChangeHasNumber(status: boolean): void {
        const addressNumber: AbstractControl = this.formAddress?.get('number');

        if (status) {
            addressNumber.disable();
            addressNumber.setValue('SN');
        } else {
            addressNumber.enable();
            addressNumber.setValue('');
            this.inputNumber.nativeElement.focus();
        }
    }

    onlyDelivery(): boolean {
        return this.company.hasDelivery && !this.company.hasWithdrawn;
    }

    onlyWithdrawn(): boolean {
        return !this.company.hasDelivery && this.company.hasWithdrawn;
    }

    isDeliveryAndWithdrawn(): boolean {
        return this.company.hasDelivery && this.company.hasWithdrawn;
    }

    addressStepNext(): void {
        if (!this.optionWithdrawalSelected && this.isDeliveryAndWithdrawn()) {
            this.matSnackBar.open('Selecione uma das opções para continuar', '', {
                duration: 2000,
                horizontalPosition: 'center',
                verticalPosition: 'top',
            });
            return;
        }

        // when withdrawal on local
        if (this.isWithdrawal() || this.onlyWithdrawn()) {
            const clientForWithdrawl = this.formData.value as Client;
            const clientLoaded = localStorage.getItem(this.localStorageUserKey);
            if (clientLoaded){
                const clientParsed: Client = JSON.parse(clientLoaded) as Client;
                const clientMerged: Client = { ...clientForWithdrawl, ...clientParsed};
                this.saveClientInLocalStorage(clientMerged);
            }else{
                this.saveClientInLocalStorage(clientForWithdrawl);
            }

            this.nextOrCloseDialog();
            return;
        }

        if (this.formAddress.invalid) {
            return;
        }

        this.nextOrCloseDialog();
        const client: Client = { ...this.formData.value, ...this.formAddress.value };
        client.phone = Number(client.phone);
        if (this.isNew) {
            this.saveClientInLocalStorage(client);
            this.clientService.save(client).subscribe(c => {
                console.log(c);
                this.isNew = false;
            });
        } else {
            const currentClientHash = this.clientHash(localStorage.getItem(this.localStorageUserKey));
            const incomingClientHash = this.clientHash(JSON.stringify(client));
            if (currentClientHash !== incomingClientHash) {
                this.saveClientInLocalStorage(client);
                this.clientService.update(client).subscribe(c => {
                    console.log(c);
                });
            }
        }
    }

    // STEP PAYMENT
    selectPayment(paymentType: PaymentType): void {
        // if (paymentType.name === 'Dinheiro') {
        //     this.matBottomSheet.open(NeedChangeMoneyComponent).afterDismissed().subscribe((valueToChange: number) => {
        //         if (valueToChange) {
        //             paymentType = {
        //                 ...paymentType,
        //                 valueToChange
        //             };

        //             this.closeDialogWithResquest(paymentType);
        //         }
        //     });
        //     return;
        // }

        this.closeDialogWithResquest(paymentType);
    }

    private isWithdrawal(): boolean {
        return this.optionWithdrawalSelected?.value === 'withdrawn';
    }

    private nextOrCloseDialog(): void {
        if (this.isEdit) {
            this.closeDialogWithResquest();
        } else {
            this.stepper.next();
        }
    }

    private closeDialogWithResquest(paymentType: PaymentType = this.requestOrderSummary.paymentType): void {
        const address: Client = this.formAddress.value as Client;
        const client: Client = { ...(this.formData.value as Client), ...address };
        const company = this.company;
        const request: RequestOrder = {
            client,
            withdrawal: this.isWithdrawal() || this.onlyWithdrawn(),
            paymentType,
            company,
            onSite: false
        };

        this.matDialogRef.close(request);
    }

    private setClientData(c: Client): void {
        this.isNew = false;
        c.hasNumber = !c.number;
        this.formData.get('name').setValue(c.name);
        this.formAddress.patchValue(c);

        if (c.hasNumber) {
            this.onChangeHasNumber(true);
        }
    }

    private clientHash(client: string): number {
        let h = 0;
        for (let i = 0; i < client.length; i++) {
            h = Math.imul(31, h) + client.charCodeAt(i) || 0;
        }
        return h;
    }

    private saveClientInLocalStorage(client: Client): void {
        localStorage.removeItem(this.localStorageUserKey);
        localStorage.setItem(this.localStorageUserKey, JSON.stringify(client));
    }

    ngAfterContentInit(): void {
        if (this.isEdit && this.requestOrderSummary.client.hasNumber) {
            const addressNumber: AbstractControl = this.formAddress?.get('number');
            addressNumber.setValue('SN');
            addressNumber.disable();
        }
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}
