import { Component, OnInit, Input, OnDestroy, Output, EventEmitter } from '@angular/core';
import { AdditionalItemsGroup } from 'src/app/shared/interfaces/additional-items-group';
import { Subject } from 'rxjs';
import { FormArray, FormGroup, FormBuilder, Validators, ValidationErrors } from '@angular/forms';
import { AdditionalItem } from 'src/app/shared/interfaces/additional-item';
import { takeUntil, filter, map } from 'rxjs/operators';

@Component({
    selector: 'app-additional-items-groups',
    templateUrl: './additional-items-groups.component.html',
    styleUrls: ['./additional-items-groups.component.scss']
})
export class AdditionalItemsGroupsComponent implements OnInit, OnDestroy {
    @Output() isGroupValid: EventEmitter<boolean> = new EventEmitter();
    @Output() additionalsSelected: EventEmitter<AdditionalItem[]> = new EventEmitter();

    @Input() group: AdditionalItemsGroup;
    @Input() isOpenedStore: boolean;

    form: FormGroup;
    additionalItems: FormArray;
    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private fb: FormBuilder
    ) { }

    ngOnInit(): void {
        this.form = this.fb.group({
            countTotal: 0,
            maxCapacity: this.group.maximumItems,
            minCapacity: this.group.minimumItems,
            additionalItems: this.fb.array([]),
        }, {
            validators: [this.maxValueValidator]
        });

        this.group.additionalItems.forEach(item => {
            this.additionalItems = this.form.get('additionalItems') as FormArray;
            this.additionalItems.push(this.createAdditionalItem(item));
        });

        this.removeValidator();
        this.setCountTotal();
        this.emitFormStatus();
        this.emitAdditionalsSelected();

        // Reset additionals
        this.additionalsSelected.emit([]);
    }

    get formAdditionalItems(): FormArray {
        return this.form.get('additionalItems') as FormArray;
    }

    createAdditionalItem(additional: AdditionalItem): FormGroup {
        return this.fb.group({
            additional: [additional, Validators.required],
            counter: [0]
        });
    }

    private emitFormStatus(): void {
        this.form.statusChanges.pipe(takeUntil(this.destroy$)).subscribe((result: string) => {
            const isValid = result === 'VALID';
            this.isGroupValid.emit(isValid);
        });
    }

    private emitAdditionalsSelected(): void {
        this.formAdditionalItems.valueChanges.pipe(takeUntil(this.destroy$)).subscribe((results: any[]) => {

            const additionalsSelected: AdditionalItem[] = results.filter(item => item.counter > 0).map(item => {
                return {
                    ...item.additional,
                    counter: item.counter
                };
            });

            this.additionalsSelected.emit(additionalsSelected);

        });
    }

    private setCountTotal(): void {
        this.formAdditionalItems.valueChanges.pipe(takeUntil(this.destroy$)).subscribe((results: AdditionalItem[]) => {
            const total = results.reduce((prevVal: number, elem: AdditionalItem) => {
                return prevVal + elem.counter;
            }, 0);

            this.form.get('countTotal').setValue(total);
        });
    }

    private removeValidator(): void {
        if (this.group.minimumItems === 0) {
            this.form.clearValidators();
        }
    }

    private maxValueValidator(fb: FormGroup): ValidationErrors | null {
        const countTotal = +fb.get('countTotal').value || 0;
        const maxCapacity = +fb.get('maxCapacity').value;
        const minCapacity = +fb.get('minCapacity').value;

        if (countTotal >= minCapacity) {
            return null;
        }

        if (maxCapacity !== countTotal) {
            return {
                maxValue:
                {
                    countTotal,
                    maxCapacity
                }
            };
        } else {
            return null;
        }
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}
