import { Component, OnInit, forwardRef, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { BaseControlValueAccessorUtils } from 'src/app/shared/services/base-control-value-accessor-utils';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { takeUntil } from 'rxjs/operators';

@Component({
    selector: 'app-input-counter',
    templateUrl: './input-counter.component.html',
    styleUrls: ['./input-counter.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => InputCounterComponent),
            multi: true
        }
    ]
})
export class InputCounterComponent extends BaseControlValueAccessorUtils implements OnInit, OnDestroy {
    @Input() disabledMax: boolean;

    count$ = new BehaviorSubject<number>(0);
    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor() {
        super();
    }

    ngOnInit(): void {
        this.count$.pipe(takeUntil(this.destroy$)).subscribe(result => {
            this.baseInput.setValue(result);
        });
    }

    remove(): void {
        if (this.currentQty() !== 0) {
            this.count$.next(this.currentQty() - 1);
        }
    }

    add(): void {
        if (!this.disabledMax) {
            this.count$.next(this.currentQty() + 1);
        }
    }

    private currentQty(): number {
        return this.count$.getValue();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}
