import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StoreComponent } from './store/store.component';
import { OpenProductDetailsComponent } from './product-details/open-product-details-component';
import { OpenCheckoutComponent } from './checkout/open-checkout.component';
import { OpenAboutComponent } from './about/open-about.component';

const routes: Routes = [
    {
        path: ':id',
        component: StoreComponent,
        children: [
            {
                path: ':productName/:productId',
                component: OpenProductDetailsComponent
            },
            {
                path: 'pedido',
                component: OpenCheckoutComponent
            },
            {
                path: 'sobre',
                component: OpenAboutComponent
            }
        ]
    },
    {
        path: '**',
        redirectTo: '/',
        pathMatch: 'full'
    }
];
@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
