import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Client } from 'src/app/shared/interfaces/client';

@Injectable()
export class ClientService {

    constructor(
        private httpClient: HttpClient
    ) { }

    get(phone: number): Observable<Client> {
        return this.httpClient.get<Client>(environment.apiUrl + `clients/${phone}`);
    }

    save(client: Client): Observable<Client> {
        return this.httpClient.post<Client>(environment.apiUrl + `clients`, client);
    }

    update(client: Client): Observable<Client> {
        return this.httpClient.put<Client>(environment.apiUrl + `clients/${client.phone}`, client);
    }
}
