import { catchError, tap } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable, BehaviorSubject, throwError } from 'rxjs';
import { Order } from 'src/app/shared/interfaces/order';
import { environment } from 'src/environments/environment';
import { Menu } from '../interfaces/menu';

@Injectable()
export class StoreService {
    private orders$ = new BehaviorSubject<Order[]>([]);
    getOrders$: Observable<Order[]> = this.orders$.asObservable();

    currentMinimunOrder$ = new BehaviorSubject<number>(null);

    // Headers
    httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    };

    constructor(
        private httpClient: HttpClient
    ) { }

    getAll(hasError$: BehaviorSubject<boolean>): Observable<Menu> {
        return this.httpClient
            .get<Menu>(`./assets/static/menu.json`, this.httpOptions)
            .pipe
            (tap(resp => {
                this.currentMinimunOrder$.next(resp.minimumOrder);
            }),
                catchError((error) => {
                hasError$.next(true);
                return throwError(error);
            }));
    }

    addOrder(order: Order): void {
        const currentValue = this.orders$.getValue();
        this.orders$.next([...currentValue, order]);
    }

    removeOrder(order: Order): void {
        const currentValue = this.orders$.getValue();
        const indexRemoved = currentValue.findIndex(item =>
            item.product.name === order.product.name &&
            item.totalPrice === order.totalPrice);

        currentValue.splice(indexRemoved, 1);
        this.orders$.next(currentValue);
    }

    totalPrice(order: Order[]): number {
        return order.reduce(
            (prevVal: any, elem: Order) => prevVal + elem.totalPrice,
            0
        );
    }

    orderQtyTotal(order: Order[]): number {
        return order.reduce(
            (prevVal: any, elem: Order) => prevVal + elem.qty,
            0
        );
    }

}
