import { Component, OnInit, Input } from '@angular/core';
import { Company } from 'src/app/shared/interfaces/company';
import { Router, ActivatedRoute } from '@angular/router';
import { AboutService } from 'src/app/shared/services/about.service';

@Component({
    selector: 'app-company-info',
    templateUrl: './company-info.component.html',
    styleUrls: ['./company-info.component.scss']
})
export class CompanyInfoComponent implements OnInit {
    @Input() company: Company;
    @Input() isOpenedStore: boolean;

    constructor(
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private aboutService: AboutService
    ) { }

    ngOnInit(): void {
    }

    openAbout(): void {
        this.aboutService.setCompany(this.company);
        this.router.navigate(['sobre'], { relativeTo: this.activatedRoute, });
    }
}
