import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectOption } from '../../interfaces/select-option';
import { MatRadioChange } from '@angular/material/radio';

@Component({
    selector: 'app-select-options',
    templateUrl: './select-options.component.html',
    styleUrls: ['./select-options.component.scss']
})
export class SelectOptionsComponent implements OnInit {
    @Input() options: SelectOption[] = [];
    @Input() optionSelected: SelectOption;
    @Output() selected: EventEmitter<SelectOption> = new EventEmitter();

    constructor() { }

    ngOnInit(): void {
    }

    onChange(optionSelected: MatRadioChange): void {
        this.selected.emit(optionSelected.value as SelectOption);
    }
}
