import { Pipe, PipeTransform } from '@angular/core';

const masks = [
    '(1',
    '(11',
    '(11) 1',
    '(11) 11',
    '(11) 111',
    '(11) 1111',
    '(11) 1111-1',
    '(11) 1111-11',
    '(11) 1111-111',
    '(11) 1111-1111',
    '(11) 11111-1111'
];

const clean = (phone: string): string => {
    return phone.replace(/[^\d\^]/gm, '');
};

const format = (phone: string): string => {
    let lastCharIndex = 0;
    const cleanValue = clean(phone);
    const charCount = cleanValue.replace(/\^/gm, '').length;
    if (charCount === 0) {
        return '';
    }
    const mask = masks[charCount - 1];
    if (charCount > 1 && !mask) {
        return '';
    }
    return mask.split('').map((c) => {
        if (c === '1') {
            if (cleanValue[lastCharIndex] === '^') {
                lastCharIndex++;
            }
            lastCharIndex++;
            return cleanValue[lastCharIndex - 1];
        } else {
            return c;
        }
    }).join('');
};

@Pipe({
    name: 'phone'
})
export class PhonePipe implements PipeTransform {
    transform(phone: string): string {
        return format(phone);
    }
}
