import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'day'
})
export class DayPipe implements PipeTransform {

    transform(value: string): string {

        const days: any = {
            SUNDAY: 'Domingo',
            MONDAY: 'Segunda',
            TUESDAY: 'Terça',
            WEDNESDAY: 'Quarta',
            THURSDAY: 'Quinta',
            FRIDAY: 'Sexta',
            SATURDAY: 'Sábado',
        };

        return days[value] || '';
    }

}
