import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { Product } from '../interfaces/product';

@Injectable()
export class ProductDetailsService {
    private productDetails$ = new BehaviorSubject<Product>(null);
    private isOpenedStore$ = new BehaviorSubject<boolean>(null);

    get$: Observable<Product> = this.productDetails$.asObservable();
    getOpenedStore$: Observable<boolean> = this.isOpenedStore$.asObservable();

    constructor(
    ) { }

    setData(product: Product): void {
        this.productDetails$.next(product);
    }

    setOpenedStore(status: boolean): void {
        this.isOpenedStore$.next(status);
    }
}
