import { Component, OnInit } from '@angular/core';
import { RequestOrder } from '../shared/interfaces/request-order';
import { MatDialogConfig, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { PricePipe } from '../shared/pipes/price.pipe';
import { Order } from '../shared/interfaces/order';
import { Company } from '../shared/interfaces/company';
import { Client } from '../shared/interfaces/client';
import { PaymentType } from '../shared/interfaces/payment-type';
import { MatSnackBar } from '@angular/material/snack-bar';
import { StoreService } from '../shared/services/store.service';
import { ActivatedRoute } from '@angular/router';
import { filter } from 'rxjs/operators';
import { DeliveryDataComponent } from '../delivery-data/delivery-data.component';
import { CheckoutService } from '../shared/services/checkout.service';
import { MatBottomSheet, MatBottomSheetConfig } from '@angular/material/bottom-sheet';
import { OrderSuccessfullyComponent } from './order-successfully/order-successfully.component';
import { LocalStorageService } from '../shared/services/local-storage.service';
import { PIX } from '../shared/utils/pix';

@Component({
    selector: 'app-checkout',
    templateUrl: './checkout.component.html',
    styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit {

    orders: Order[];
    company: Company;
    totalPrice: number;
    showDatas: boolean;
    requestOrder: RequestOrder;

    client: Client;
    addressValue: string;
    paymentValue: string | PaymentType;
    withdrawal: boolean;
    hasAddress: boolean;

    pixPattern1 = "00020126360014BR.GOV.BCB.PIX0114605200610005355204000053039865406"
    pixPattern2 = "5802BR5911Santa Clara6007Taubate62070503***63047E97"

    constructor(
        private matDialog: MatDialog,
        private matDialogRef: MatDialogRef<CheckoutComponent>,
        private pricePipe: PricePipe,
        private matSnackBar: MatSnackBar,
        private storeService: StoreService,
        private checkoutService: CheckoutService,
        private matBottomSheet: MatBottomSheet,
        private localStorageService: LocalStorageService
    ) {
        this.checkoutService.get$.subscribe(res => {
            if (!res) {
                this.matDialogRef.close();
                return;
            }

            this.orders = res.ordersAdded;
            this.totalPrice = res.orderPriceTotal;
            this.company = res.company;
        });
    }

    ngOnInit(): void {
        this.localStorageService.getStorage('orderFrom').subscribe((res) => {
            this.requestOrder = {
                company: this.company,
                withdrawal: false,
                onSite: res != null && res !== 'delivery' ? true : false,
                orderFrom: res
            };
        });
        this.showDatas = false;
    }

    deletedItem(order: Order): void {
        this.storeService.removeOrder(order);
        this.totalPrice = this.storeService.totalPrice(this.orders);

        if (this.orders.length === 0) {
            this.matSnackBar.open('Seu pedido está vazio', '', {
                duration: 2000,
                horizontalPosition: 'center',
                verticalPosition: 'top',
            });

            this.close();
        }
    }

    close(): void {
        this.matDialogRef.close();
    }

    setMask(): string {
        const status: boolean = this.client.phone.toString().length === 10;
        return status ? '(00) 0000-0000' : '(00) 00000-0000';
    }

    editInfo(stepIndex: number): void {
        const dialogConfig: MatDialogConfig = {
            data: {
                selectedIndex: stepIndex,
                requestOrder: this.requestOrder,
                company: this.company
            },
            autoFocus: false
        };

        this.dialogOpen(dialogConfig).subscribe(res => this.setValues(res));
    }

    confirmData(): void {
        const dialogConfig: MatDialogConfig = {
            data: {
                company: this.company
            },
            autoFocus: false
        };

        this.dialogOpen(dialogConfig).subscribe(res => this.setValues(res));
    }

    sendWhatsApp(): void {
        this.requestOrder.orders = this.orders;

        if (!this.requestOrder.withdrawal) {
            const total = this.checkoutService.sumTotal(this.requestOrder);
            if (total < this.storeService.currentMinimunOrder$.value) {
                const minimumValue = this.pricePipe.transform(this.storeService.currentMinimunOrder$.value);
                this.matSnackBar
                    .open(`Seu pedido não atingiu o valor mínimo para entrega. Valor mínimo para entrega: ${minimumValue}`, '', {
                        duration: 2000,
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                return;
            }
        }

        const config: MatBottomSheetConfig = {
            disableClose: true
        };

        this.matBottomSheet.open(OrderSuccessfullyComponent, config);

        setTimeout(() => {
            this.checkoutService.generateOrder(this.requestOrder).subscribe(url => {
                setTimeout(() => {
                    document.location.href = url;
                }, 250);
            });
        }, 2000);
    }

    copyPix() {
        let pix = PIX.static()
                     .setReceiverName('Convento Santa Clara')
                     .setReceiverCity('Taubaté')
                     .setKey('60520061000535')
                     .setAmount((this.totalPrice/100)) // optional
        const el = document.createElement('textarea');
        el.value = pix.getBRCode();
        document.body.appendChild(el);
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
        alert("Pix Copiado!! Agora é só colar na área PIX COPIA E COLA do app seu banco!")
    }

    private setValues(result: RequestOrder): void {
        this.requestOrder = result;
        this.showDatas = true;

        this.adapterViewer(result);
    }

    private adapterViewer(res: RequestOrder): void {
        this.client = {
            name: `${res.client.name}\n`,
            phone: res.client.phone
        };

        const address = `${res.client.address}, ${res.client.number ? res.client.number : 'SN'} - ${res.client.area}`;

        this.addressValue = res.withdrawal ? 'Retirada no local' : address;
        this.hasAddress = !res.withdrawal;

        this.paymentValue = res.paymentType.name === 'Dinheiro' ?
            (`${res.paymentType.name}\n Troco para: ${this.pricePipe.transform(res.paymentType.valueToChange)}`) :
            res.paymentType.name;
    }

    private dialogOpen(dialogConfig: MatDialogConfig = {}): Observable<RequestOrder> {
        dialogConfig.panelClass = 'dialog-fullScreen';

        return this.matDialog.open(DeliveryDataComponent, dialogConfig)
            .afterClosed().pipe(filter((val) => !!val));
    }

}
