import { Component, OnInit } from '@angular/core';
import { Order } from 'src/app/shared/interfaces/order';
import { MatDialogRef } from '@angular/material/dialog';
import { Product } from 'src/app/shared/interfaces/product';
import { FormGroup, FormBuilder } from '@angular/forms';
import { BehaviorSubject, Observable } from 'rxjs';
import { AdditionalItemsGroup } from 'src/app/shared/interfaces/additional-items-group';
import { AdditionalItem } from 'src/app/shared/interfaces/additional-item';
import { Utils } from 'src/app/shared/utils/utils';
import { ProductDetailsService } from '../shared/services/product-details.service';

@Component({
    selector: 'app-product-details',
    templateUrl: './product-details.component.html',
    styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {

    product: Product;
    isOpenedStore$: Observable<boolean>;
    priceTotal: number;
    form: FormGroup;
    isAllGroupsInvalid$ = new BehaviorSubject<boolean>(false);

    constructor(
        private matDialogRef: MatDialogRef<ProductDetailsComponent>,
        private formBuilder: FormBuilder,
        private productDetailsService: ProductDetailsService
    ) {
        this.productDetailsService.get$.subscribe(res => {
            if (!res) {
                this.matDialogRef.close();
                return;
            }

            this.product = Utils.removeReference(res);
            this.priceTotal = this.product?.price;
        });

        this.isOpenedStore$ = this.productDetailsService.getOpenedStore$;
    }

    ngOnInit(): void {
        this.form = this.formBuilder.group({
            additionals: [],
            observation: [],
        });

        if (this.product?.additionalItemsGroups.length > 0) {
            this.product.additionalItemsGroups.forEach(itemGroup => {
                itemGroup.isValid = itemGroup.minimumItems === 0;
                itemGroup.groupPriceTotal = 0;
            });

            this.setAllGroupsInvalid();
        }
    }

    enableAddButton(groupValid: boolean, indexPositionGroup: number): void {
        this.product.additionalItemsGroups[indexPositionGroup].isValid = groupValid;
        this.setAllGroupsInvalid();
    }

    changeAdditionalItems(additionalItems: AdditionalItem[], indexPositionGroup: number): void {
        const group = this.groupSelected(indexPositionGroup);

        // Additional Items Value
        const additionalItemsValueTotal: number[] = additionalItems.map((addicional: AdditionalItem) => {
            return addicional.price * addicional.counter;
        });

        group.additionalItems = additionalItems;

        // Group Value Total
        const groupValueTotal = additionalItemsValueTotal.reduce((prevVal: number, pao: number) => {
            return (prevVal + pao);
        }, 0);

        group.groupPriceTotal = groupValueTotal;

        this.setValueTotal();
    }

    private setValueTotal(): void {
        const totalAdditionalItemsGroups = this.product.additionalItemsGroups.reduce((prevVal: number, groupItem: any) => {
            return (prevVal + groupItem.groupPriceTotal);
        }, 0);

        this.priceTotal = this.product.price + totalAdditionalItemsGroups;
    }

    orderAdded(order: Order): void {
        order.product = this.product;
        order.observation = this.form.get('observation').value;
        order.product.price = this.priceTotal;

        this.matDialogRef.close(order);
    }

    private groupSelected(indexPositionGroup: number): AdditionalItemsGroup {
        return this.product.additionalItemsGroups[indexPositionGroup];
    }

    private setAllGroupsInvalid(): void {
        const allGroupsStatus: boolean = this.product.additionalItemsGroups.every(group => group.isValid);
        this.isAllGroupsInvalid$.next(!allGroupsStatus);
    }
}
